@import url(https://fonts.googleapis.com/css2?family=Signika+Negative&display=swap);
:root {
  --size: 50px;
  --grid-interact: auto;
  --nav-height: 75px;
  --node-cursor: grab;
  --grid-cursor: pointer;
  --font-size: 12px;
  --visibility: hidden;
}

body {
  --toggle: 1;
  --playState: "idle";
  --animation-speed: 0.5s;
  --animation-speed-path: 0.5s;
  --animation-speed-visited: 500ms;
  --visit-delay: 100ms;
}

html,
body,
main {
  margin: 0;
  height: 100%;
  font-family: "Signika Negative", sans-serif;
}

.nav {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 75px;
  height: var(--nav-height);
  background-color: rgb(18, 40, 48);
}
.nav-title {
  color: white;
  margin: 0 10px;
  width: 350px;
}
.clear-button {
  color: white;
}
.clear-button button:focus {
  outline: none;
  box-shadow: none;
}
.clear-button button {
  background-color: white;
  margin: 5px;
  padding: 5px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.algo-button button:focus {
  outline: none;
  box-shadow: none;
}
.algo-button button {
  color: white;
  background-color: rgb(61, 156, 112);

  margin: 5px;
  padding: 5px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.view-distance {
  font-size: 10px;
  color: white;
}
.settings-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  margin-left: 20px;
  margin-bottom: 10px;
  height: 100%;
  width: 100%;
  color: white;
}
.settings {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-left: 100px;
  color: white;
  height: 4px;
}
.slider {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-right: 10px;
}
input {
  cursor: pointer;
}
.speed {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-right: 10px;
}

.loader {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: calc(100vh - 75px);
  height: calc(100vh - var(--nav-height));
}
.loader h3 {
  font-size: 100px;
  height: 100px;
}

.grid-container {
  display: -webkit-flex;
  display: flex;
  height: calc(100vh - 75px);
  height: calc(100vh - var(--nav-height));
}
.grid {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 100%;
  height: 100%;
  cursor: pointer;
  cursor: var(--grid-cursor);
}
.grid-table {
  border-collapse: collapse;
  border-spacing: 0;
  pointer-events: auto;
  pointer-events: var(--grid-interact);
}

.unvisited {
  cursor: pointer;
  cursor: var(--grid-cursor);
  background-color: white;
}

td {
  border: 1px solid lightblue;
  height: 50px;
  height: var(--size);
  width: 50px;
  width: var(--size);
}
.specs {
  color: rgb(13, 13, 39);
  font-size: 12px;
  font-size: var(--font-size);
  visibility: hidden;
  visibility: var(--visibility);
  pointer-events: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
}

@-webkit-keyframes visitedStartNodeBlueAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

