.start-node {
  border-radius: 50px;
  border-radius: var(--size);
  cursor: -webkit-grab;
  cursor: grab;
  cursor: var(--node-cursor);
  background-image: url(data:image/png;base64,);

  background-size: contain;

  -webkit-animation-name: startNodeAnimation;

          animation-name: startNodeAnimation;
  -webkit-animation-duration: calc(var(--toggle) * var(--animation-speed));
          animation-duration: calc(var(--toggle) * var(--animation-speed));
  -webkit-animation-play-state: var(--playState);
          animation-play-state: var(--playState);
}
